import styled, { css } from "styled-components";

export interface SegmentedButtonProps {
  /**
   * @default medium
   * @description
   * small: min-height=20, vertical-padding=2,  horizontal-padding=8
   * medium: min-height=28, vertical-padding=4, horizontal-padding=12
   * large: min-height=36, vertical-padding=6, horizontal-padding=16
   */
  $size?: "small" | "medium" | "large";
  /** @default false */
  $active?: boolean;
  children?: React.ReactNode;
}

/** @design https://www.figma.com/design/JGFHYME2p7RpeMYcoMqooS/ODE-Library-1.0?node-id=11429-14859&t=yPRe6bLPZnxitmAJ-4 */
export const SegmentedButton = styled.button.attrs({
  type: "button",
})<SegmentedButtonProps>`
  ${(p) => p.theme.button.reset}
  ${(p) => p.theme.flex.row}
  ${(p) => p.theme.flex.center}
  flex: none;
  min-width: ${(p) => getWidth(p.$size)}px;
  min-height: ${(p) => getHeight(p.$size)}px;
  padding: ${(p) => getPadding(p.$size)};
  border-radius: ${(p) => p.theme.radius.xxxxs}px;
  ${(p) => getFont(p.$size)}
  background-color: transparent;
  color: ${(p) => p.theme.colors.noColor.Grey9};
  transition: color 0.2s ease, background-color 0.2s ease, box-shadow 0.2s ease;

  &:hover {
    background-color: ${(p) => p.theme.colors.themeColor.Air4};
    color: ${(p) => p.theme.colors.textInput.default};
  }

  ${({ $active, theme, disabled }) => {
    if (!disabled && $active) {
      return css`
        background-color: #fff;
        color: ${theme.colors.textInput.default};
        box-shadow: ${theme.colors.softShadow};

        &:hover {
          background-color: #fff;
          color: ${theme.colors.textInput.default};
          box-shadow: ${theme.colors.softShadow};
        }
      `;
    }
    return undefined;
  }}

  &:disabled {
    background-color: ${(p) => p.theme.colors.button.disabled};
    color: ${(p) => p.theme.colors.buttonInput.disabled};
    cursor: not-allowed;
    box-shadow: ${(p) => (p.$active ? p.theme.colors.softShadow : "none")};

    &:hover {
      background-color: ${(p) => p.theme.colors.button.disabled};
      color: ${(p) => p.theme.colors.buttonInput.disabled};
    }
  }
`;

function getWidth(size: SegmentedButtonProps["$size"] = "medium") {
  switch (size) {
    default:
    case "medium":
      return 40;
    case "small":
      return 32;
    case "large":
      return 48;
  }
}

function getHeight(size: SegmentedButtonProps["$size"] = "medium") {
  switch (size) {
    default:
    case "medium":
      return 28;
    case "small":
      return 20;
    case "large":
      return 36;
  }
}

function getPadding(size: SegmentedButtonProps["$size"] = "medium") {
  switch (size) {
    default:
    case "medium":
      return "4px 12px";
    case "small":
      return "2px 8px";
    case "large":
      return "6px 16px";
  }
}

function getFont(size: SegmentedButtonProps["$size"] = "medium") {
  switch (size) {
    default:
    case "medium":
      return css`
        ${(p) => p.theme.fonts["Body-Medium/Regular"]};
        line-height: 20px;
      `;
    case "small":
      return css`
        ${(p) => p.theme.fonts["Caption-Large/Regular"]}
        line-height: 16px;
      `;
    case "large":
      return css`
        ${(p) => p.theme.fonts["Body-Medium/Bold"]};
        line-height: 24px;
      `;
  }
}
